.App {
  text-align: center;
  padding: 16px;
  margin: 10px;
}
.image{
  width: 70%;
  height: 10%;
}
.footer{
  border: 1px solid #eee;
  -webkit-box-shadow: 3px 3px #ccc;
          box-shadow: 3px 3px #ccc;
}
#myimage{
  width: 60px;
  height: 60px;
  border-radius: 10%;
}
#myname{
  font-weight: bold;
}
.Container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.CardsContainer{
  margin: 50px 0;
}
.Charts{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 85%;
}
.Card{
  margin: 0 2% !important;
  border: 1px solid black;
  text-align: center;
}
#confirmed{
  color: red;
  border-bottom: 10px solid rgba(255,0,0,0.5);
}
#active{
  color: blue;
  border-bottom: 10px solid rgba(0,0,255,0.5);
}
#recovered{
  color: green;
  border-bottom: 10px solid rgba(0,255,0,0.5);
}
#deceased{
  color: rgb(87, 84, 84);
  border-bottom: 10px solid rgba(87,84,84,0.5);
}
#confirmed:hover{
  color: white;
  background-color: tomato;
  -webkit-box-shadow: 8px 8px rgb(207, 203, 203);
          box-shadow: 8px 8px rgb(207, 203, 203);
}
#active:hover{
  color:white;
  background-color: cornflowerblue;
  -webkit-box-shadow: 8px 8px rgb(207, 203, 203);
          box-shadow: 8px 8px rgb(207, 203, 203);
}
#recovered:hover{
  color: white;
  background-color: rgb(76, 190, 76);
  -webkit-box-shadow: 8px 8px rgb(207, 203, 203);
          box-shadow: 8px 8px rgb(207, 203, 203);
}
#deceased:hover{
  background-color: grey;
  color: white;
  -webkit-box-shadow: 8px 8px rgb(207, 203, 203);
          box-shadow: 8px 8px rgb(207, 203, 203);
}
.footer:hover{
  -webkit-box-shadow: 8px 8px rgb(207, 203, 203);
          box-shadow: 8px 8px rgb(207, 203, 203);
}

@media (max-width: 770px){
  .Container{
    margin: 0 10%;
  }
  .image{
    width: 100%;
  }
  .Card{
    margin: 2% 0 !important;
  }
  .Charts{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
}


